    <template v-for="(img, index) in images">
      <ImgBox :image-source="img.src" :image-title="img.title" :image-destination="img.destination" :key="index"></ImgBox>
    </template>
  </div>
  <div id="photoButton">
    <MainButton :link="buttonLink" :button-text="buttonText"></MainButton>
  </div>
</div>

</template>

<script>

import MainButton from "@/components/buttons/MainButton";
import SectionTitle from "@/components/titles/SectionTitle";
import ImgBox from "@/components/imgBox/ImgBox";

export default {
  name: "SectionPhoto",
  data() {
    return {
      heading: "Foto",
      buttonText: "Zeig mir mehr",
      buttonLink: "/hochzeitsfotografie",
      images: [
        {
          src: "/img/ulyana-tim-unsplash.jpg",
          title: "Berlin",
          destination: "/hochzeitsfotografie"
        },
        {
          src: "/img/beatriz-perez-moya-unsplash.jpg",
          title: "Köln",
          destination: "/hochzeitsfotografie"
        },
        {
          src: "/img/foto-pettine-unsplash.jpg",
          title: "München",
          destination: "/hochzeitsfotografie"
        },
        {
          src: "/img/hannah-busing-unsplash.jpg",
          title: "Hamburg",
          destination: "/hochzeitsfotografie"
        },
        {
          src: "/img/sandy-millar-unsplash.jpg",
          title: "Bremen",
          destination: "/hochzeitsfotografie"
        },
        {
          src: "/img/ulyana-tim-unsplash.jpg",
          title: "Düsseldorf",
          destination: "/hochzeitsfotografie"
        },
      ]
    }
  },
  components: {
    ImgBox,
    SectionTitle,
    MainButton
  },
  mounted() {
    /* EDIT SLIDER SETTINGS HERE */
    /* eslint-env jquery */
    $(document).ready(() => {
      $('.center').slick({
        dots: true,
        arrows: true,
        infinite: true,
        slidesToShow: 3,
        slidesToScroll: 3,
        touchThreshold: 50,
        responsive: [
          {
            breakpoint: 1201,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              infinite: true,
              dots: true,
              arrows: false
            }
          },
          {
            breakpoint: 601,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1,
              infinite: true,
              dots: true,
              arrows: false
            }
          },
        ]
      });
    });
  }
}

