<style>

.slick-slide {
  margin: 0;
}

.slick-list {
  height: max-content;
}

.slick-prev:before {
  content: "<";
  color: #000000;
  font-size: 30px;
}

.slick-next:before {
  content: ">";
  color: #000000;
  font-size: 30px;
}

.slick-dots {
  margin: 0;
  display: flex;
  justify-content: center;
  list-style-type: none;
}

.slick-dots li {
  margin: 0;
  padding: 0;
  height: max-content;
}

.slick-dots li button:before {
  content: "⬤";
  opacity: 0.4;
}

.slick-dots li.slick-active button {
  opacity: 1;
}

</style>

<style scoped>

.center {
  display: flex;
  width: 90vw;
  margin: 0 auto;
