<template>

<div class="menu" v-bind:style="navBarColor">

  <LogoMenu v-bind:logo-url="logoUrl"></LogoMenu>

  <template v-for="(point, index) in menu">
    <SingleMenuPoint v-if="point.main"
                     v-bind:key="'single' + index"
                     v-bind:title="point.title"
                     v-bind:link="point.link"
    ></SingleMenuPoint>
    <SubMenuPoints v-else
                   v-bind:key="'single' + index"
                   v-bind:title="point.title"
                   v-bind:links="point.link"
                   v-bind:sub-points-bg-color="subPointsBgColor"
    ></SubMenuPoints>
  </template>

</div>

</template>

<script>

import menu from "@/menu";
import SingleMenuPoint from "@/components/navBar/desktop/MenuSinglePoint";
import SubMenuPoints from "@/components/navBar/desktop/MenuSubPoints";
import LogoMenu from "@/components/navBar/desktop/MenuLogo";

export default {
  name: "ViewDesktopNavBar",
  components: {
    LogoMenu,
    SubMenuPoints,
    SingleMenuPoint
  },
  data() {
    return {
      menu: menu,
    }
  },
  props: [
      'navBarColor',
      'menuPoints',
      'subPointsBgColor',
      'logoUrl'
  ]
}

</script>

<style scoped>

.menu {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0 10%;
  z-index: 1;
  height: var(--nav-bar-height);
}

</style>