    }
  },
  mounted() {
    /* EDIT SLIDER SETTINGS HERE */
    /* eslint-env jquery */
    $(document).ready(() => {
      $('.slider').slick({
        arrows: false,
        autoplay: true,
        autoplaySpeed: 4000,
        dots: false,
        easing: 'ease-in-out',
        pauseOnFocus: 'false',
        pauseOnHover: 'false',
        speed: 2000,
        touchThreshold: 50
      });
    });

    /* CALCULATE & SET INITIAL VH VALUE */
    //let vh = window.innerHeight * 0.01;
    //document.documentElement.style.setProperty('--vh', `${vh}px`);

