<template>

<div class="page videoPage">
  <NavBar></NavBar>
  <MainTitle :title="heading"></MainTitle>
  <div class="allVideos">
  <template v-for="(video, index) in videos">
    <div class="wholeWrapper" v-bind:key="index">
      <div class="iframeWrapper">
          <svg class="foregroundSVG" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-on:click="playVideo($event, video.videoWrapperID, video.thumbnailID, index)">
            <!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. -->
            <path fill="white" d="M512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM176 168V344C176 352.7 180.7 360.7 188.3 364.9C195.8 369.2 205.1 369 212.5 364.5L356.5 276.5C363.6 272.1 368 264.4 368 256C368 247.6 363.6 239.9 356.5 235.5L212.5 147.5C205.1 142.1 195.8 142.8 188.3 147.1C180.7 151.3 176 159.3 176 168V168z"/>
          </svg>
          <img :id="video.thumbnailID" :src="video.thumbnailSrc" :alt="video.thumbnailAlt">
          <div class="videoWrappers" :id="video.videoWrapperID">
            <youtube ref="youtube" :video-id="video.videoYTID" width="1000" height="563" :fit-parent="fitParent" :resize="resize" :resize-delay="resizeDelay" @ready="ready(video.bgSVGID, video.videoWrapperID)"></youtube>
          </div>
      </div>
      <div class="videoDescription">
        <img class="loaderGif" :id="video.bgSVGID" src="/img/loading.gif" alt="GIF Loader">
        <div class="videoTitle">{{ video.videoTitle }}</div>
        <hr>
        <div class="videoSubtitle">{{ video.videoSubtitle }}</div>
      </div>
    </div>
  </template>
  </div>
  <ContactUs></ContactUs>
  <InstaFeed></InstaFeed>
  <MainFooter></MainFooter>
</div>

</template>

<script>

import NavBar from "@/components/navBar/NavBar";
import MainTitle from "@/components/titles/MainTitle";
import MainFooter from "@/components/footer/MainFooter";
import ContactUs from "@/components/contactUs/ContactUs";
import InstaFeed from "@/components/socialMedia/InstaFeed";


export default {
  name: "VideoPage",
  components: {
    InstaFeed,
    ContactUs,
    MainFooter,
    NavBar,
    MainTitle
  },
  data() {
    return {
      bgSVGcolor: "#f2eeea",
      heading: "Video Gallerie",
      videoId: "szYSPzGZ8lk",
      fitParent: true,
      resize: true,
      resizeDelay: 0,
      noCookie: true,
      videos: [
        {
          videoYTID: "szYSPzGZ8lk",
          videoTitle: "XXX & YYY",
          videoSubtitle: "Some fancy description",
          thumbnailSrc: "/img/beatriz-perez-moya-unsplash.jpg",
          thumbnailAlt: "Image Description",
          videoWrapperID: "vwID1",
          thumbnailID: "tnID1",
          bgSVGID: "bgID1",
        },
        {
          videoYTID: "uQuz47-6JxE",
          videoTitle: "XXX & YYY",
          videoSubtitle: "Some fancy description",
          thumbnailSrc: "/img/sandy-millar-unsplash.jpg",
          thumbnailAlt: "Image Description",
          videoWrapperID: "vwID2",
          thumbnailID: "tnID2",
          bgSVGID: "bgID2",
        },
        {
          videoYTID: "3rYz30PKlS0",
          videoTitle: "XXX & YYY",
          videoSubtitle: "Some fancy description",
          thumbnailSrc: "/img/hannah-busing-unsplash.jpg",
          thumbnailAlt: "Image Description",
          videoWrapperID: "vwID3",
          thumbnailID: "tnID3",
          bgSVGID: "bgID3",
        }
      ]
    }
  },
  computed: {
    player() {
      return this.$refs.youtube.player
    }
  },
  methods: {
    ready: function(bgSVGID, videoWrapperID) {
      document.getElementById(bgSVGID).style.display = "none";
      document.getElementById(videoWrapperID).style.display = "block";
    },
    playVideo: function(event, videoWrapperID, thumbnailID, index) {
      event.target.style.display = "none";
      document.getElementById(thumbnailID).style.display = "none";
      let wrapper = document.getElementById(videoWrapperID);
      wrapper.style.display = "block";
      this.$refs.youtube[index].player.playVideo()
    },
  },
}

</script>

<style scoped>

.allVideos {
  max-width: 1000px;
  margin: 0 auto;
}

.iframeWrapper {
  width: 100%;
  height: 100%;
  position: relative;
  max-width: 1000px;
  max-height: 563px;
  margin: 0 auto;
}

.iframeWrapper img {
  position: absolute;
  width: 100%;
  height: 100%;
  max-width: 1000px;
  max-height: 563px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  z-index: 15;
  box-shadow:  -2vw 0 0 white, 2vw 0 0 white;
}

.videoWrappers {
  display: none;
}

.loaderGif {
  margin: auto;
  padding: 5% 0;
}

.foregroundSVG {
  position: absolute;
  width: 100%;
  height: 20%;
  top: 40%;
  z-index: 50;
  cursor: pointer;
}

.videoDescription {
  text-align: center;
}

.videoTitle {
  margin-top: 10px;
  font-size: 2.5vw;
}

.videoSubtitle {
  text-transform: uppercase;
  font-size: 1.5vw;
  margin-bottom: 50px;
}

hr {
  margin: 10px auto;
  width: 70%;
}

@media screen and (max-width: 1280px) {

  .wholeWrapper  {
    max-width: 90%;
    margin: 0 auto;
  }

  .videoTitle {
    font-size: 3vw;
  }

  .videoSubtitle {
    font-size: 2vw;
  }

}

@media screen and (max-width: 850px) {

  .videoTitle {
    font-size: 3.5vw;
  }

  .videoSubtitle {
    font-size: 2.5vw;
  }

}

@media screen and (max-width: 600px) {

  .loaderGif {
    height: 32px;
  }

  .videoTitle {
    font-size: 5vw;
  }

  .videoSubtitle {
    font-size: 4vw;
  }

}

</style>