<template>

<div class="contactBG">
  <div class="contactWrapper">
    <!-- HEAD -->
    <div class="cTitle">{{ heading1 }}</div>
    <div class="cSubtitle">{{ heading2 }}</div>
    <form>
      <!-- REASON - SELECT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="reason">Anlass <span>*</span></label>
        <select id="reason" name="reason" v-model="form.reason">
          <option disabled hidden value="">Wähle aus</option>
          <template v-for="(reason, index) in selectReasons">
            <option :value="reason" v-bind:key="index">{{ reason }}</option>
          </template>
        </select>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- NAME - TEXT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="name">Name <span>*</span></label>
        <input id="name" name="name" v-model="form.name" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- EMAIL - TEXT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="email">E-Mail <span>*</span></label>
        <input id="email" name="email" v-model="form.email" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- PHONE - TEXT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="phone">Telefon <span>*</span></label>
        <input id="phone" name="phone" v-model="form.phone" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- DATE - DATE -->
      <div class="cRow">
        <label class="cLabelGeneral" for="date">Wunschdatum <span>*</span></label>
        <!-- <input id="date" name="date" v-model="form.date" type="text" placeholder="TT.MM.JJJJ" /> -->
        <datepicker v-model="form.date"
                    name="date"
                    type="text"
                    input-class="dpInput"
                    format="dd.MM.yyyy"
                    placeholder="TT.MM.JJJJ"
                    monday-first id="date"
                    :language="de"
        ></datepicker>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- LOCATION - TEXT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="location">Location(s) <span>*</span></label>
        <input id="location" name="location" v-model="form.location" type="text" />
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- SERVICE - SELECT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="service">Wir interessieren uns für... <span>*</span></label>
        <select id="service" name="service" v-model="form.service">
          <option disabled hidden value="">Wähle aus</option>
          <template v-for="(service, index) in selectServices">
            <option :value="service" v-bind:key="index">{{ service }}</option>
          </template>
        </select>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- STYLE - CHECKBOX RADIO -->
      <div class="cRow">
        <label class="cLabelGeneral">Stil der Hochzeit</label>
        <div class="cCheckboxes">
          <template v-for="(style, index) in checkboxesStyle">
            <div class="cCheckbox" v-bind:key="index">
              <input type="radio" :id="style.id" :value="style.style" v-model="form.style" />
              <label :for="style.id">{{ style.style }}</label>
            </div>
          </template>
        </div>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- MESSAGE - TEXTAREA -->
      <div class="cRow">
        <label class="cLabelGeneral" for="message">Nachricht <span>*</span></label>
        <textarea id="message" name="message" v-model="form.message" type="text"></textarea>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- NOTICE - SELECT -->
      <div class="cRow">
        <label class="cLabelGeneral" for="notice">Wie habt ihr uns gefunden? <span>*</span></label>
        <select id="notice" name="notice" v-model="form.notice">
          <option disabled hidden value="">Wähle aus</option>
          <template v-for="(notice, index) in selectNotices">
            <option :value="notice" v-bind:key="index">{{ notice }}</option>
          </template>
        </select>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- PRIVACY - CHECKBOX -->
      <div class="cRow">
        <label class="cLabelGeneral">Datenschutz <span>*</span></label>
        <div class="cTerms">
          <input type="checkbox" id="terms" v-model="form.terms" />
          <label for="terms">{{ privacyMessage }}</label>
        </div>
      </div>
      <div class="cRequiredMessages">{{ requiredMessage }}</div>
      <!-- BUTTON -->
      <button id="cButton" type="button" v-on:click="sendMail">{{ buttonMessage }}</button>
      <!-- LOADER -->
      <div id="cLoader">
        <img :src="loaderGIF" alt="Loader GIF">
        <div>{{ loaderMessage }}</div>
      </div>
      <!-- SUCCESS -->
      <div id="cSuccess" class="sentBox">
        {{ successMessage }} <router-link to="/">{{ sentMessage }}</router-link>
      </div>
      <!-- ERROR -->
      <div id="cError" class="sentBox">
        {{ errorMessage }} <router-link to="/">{{ sentMessage }}</router-link>
      </div>
    </form>
  </div>
</div>

</template>

<script>

import axios from "axios";
import Datepicker from 'vuejs-datepicker';
import { de } from 'vuejs-datepicker/dist/locale';

export default {
  name: "ContactForm1",
  components: { Datepicker },
  props: [ "heading1" ],
  data() {
    return {
      de: de,
      errors: 0,
      heading2: "Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.",
      buttonMessage: "Nachricht senden",
      requiredMessage: "Dieses Feld ist erforderlich!",
      privacyMessage: "Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.",
      loaderGIF: "/img/loading.gif",
      loaderMessage: "Nachricht wird gesendet. Bitte warten...",
      errorMessage: "Deine Nachricht konnte leider nicht versendet werden. Bitte versuche es später erneut. ",
      successMessage: "Vielen Dank für Deine Nachricht! Wir werden uns in Kürze bei Dir melden. ",
      sentMessage: "Zur Startseite",
      checkboxesStyle: [
        { id: "klassisch", style: "Klassisch" },
        { id: "vintage", style: "Vintage" },
        { id: "boho", style: "Boho" },
        { id: "glam", style: "Glam" }
      ],
      selectReasons: [ "Hochzeitsreportage", "Standesamtliche Trauung", "Sonstiger Anlass" ],
      selectServices: [ "Foto", "Video", "Foto & Video" ],
      selectNotices: [ "Empfehlung", "Instagram", "Facebook", "Google", "Sonstiges" ],
      form: {
        reason: "",
        name: "",
        email: "",
        phone: "",
        date: "",
        location: "",
        service: "",
        style: "",
        message: "",
        notice: "",
        terms: ""
      },
    }
  },
  methods: {
    checkForm: function() {
      let requiredMessages = document.querySelectorAll(".cRequiredMessages");
      let counter = 0;
      this.errors = 0;

      requiredMessages.forEach(reqField => {
        reqField.style.display = "none";
      });

      // IMPORTANT: MUST BE IN THE ORDER OF THE LABELS IN HTML
      if (this.form.reason === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.name === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.email === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.phone === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.date === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.location === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.service === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      //if (this.form.style === "") {
      //  this.errors += 1;
      //  requiredMessages[counter].style.display = "flex";
      //}
      counter++;
      if (this.form.message === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.notice === "") {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
      counter++;
      if (this.form.terms !== true) {
        this.errors += 1;
        requiredMessages[counter].style.display = "flex";
      }
    },
    sendMail: function() {
      this.checkForm()
      if (this.errors === 0) {
        document.getElementById("cButton").style.display = "none";
        document.getElementById("cLoader").style.display = "flex";
        axios.post("http://127.0.0.1:5000/send", this.form)
        .then(function() {
          document.getElementById("cLoader").style.display = "none";
          document.getElementById("cSuccess").style.display = "block";
        })
        .catch(function() {
          document.getElementById("cLoader").style.display = "none";
          document.getElementById("cError").style.display = "block";
        });
      }
    }
  },
}

</script>

<style>

/* MUST BE SAME WITH SECTION ABOVE: input, select, textarea */
.dpInput {
  width: 100%;
  display: flex;
  padding: 5px;
  border: 1px solid #b5aba7;
  letter-spacing: 1px;
  font-family: var(--main-website-font-family);
  font-size: 20px;
  transition: 0.25s;
}
.dpInput:hover {
  box-shadow: 1px 1px 5px #c2bebb, -1px -1px 5px #eee8e5;
  transition: 0.25s;
}
.dpInput:focus {
  outline: none;
  box-shadow: 1px 1px 5px #c2bebb, -1px -1px 5px #eee8e5;
  transition: 0.25s;
}

</style>

<style scoped>

.contactBG {
  width: 100%;
  background-color: #f2eeea;
}

.contactWrapper {
  width: 70%;
  margin: 300px auto 0 auto;
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  position: relative;
  background-color: white;
}

form {
  width: 75%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.cRow {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 10px 0 0 0;
}

.cRequiredMessages {
  display: none;
  width: 100%;
  margin: 5px 0;
  font-weight: bold;
  text-align: left;
  letter-spacing: 1px;
  color: red;
}

.cTitle {
  width: 80%;
  text-align: center;
  font-family: var(--main-title-font-family);
  font-size: 50px;
}

.cSubtitle {
  width: 70%;
  padding: 15px 0;
  text-align: center;
  line-height: 1.4;
  font-size: 20px;
}

.cLabelGeneral {
  width: 100%;
  padding: 20px 0 10px 0;
  text-align: left;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
  font-size: 15px;
}

input[type=text], select, textarea {
  display: flex;
  padding: 5px;
  border: 1px solid #b5aba7;
  letter-spacing: 1px;
  font-family: var(--main-website-font-family);
  font-size: 20px;
  transition: 0.25s;
}

textarea {
  height: 200px;
  resize: none;
}

input[type=text]:hover, select:hover, textarea:hover {
  box-shadow: 1px 1px 5px #c2bebb, -1px -1px 5px #eee8e5;
  transition: 0.25s;
}

input[type=text]:focus, select:focus, textarea:focus {
  outline: none;
  box-shadow: 1px 1px 5px #c2bebb, -1px -1px 5px #eee8e5;
  transition: 0.25s;
}

/* RADIO Checkboxes */
.cCheckboxes {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.cCheckbox {
  width: 100%;
  margin: 10px auto 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cCheckbox label {
  width: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  padding-left: 5px;
  font-size: 20px;
}

/* PRIVACY Checkbox */
.cTerms {
  width: 100%;
  display: flex;
}
.cTerms label {
  padding-left: 5px;
  text-align: justify;
}

/* BUTTON */
button {
  width: max-content;
  margin: 30px auto 0 auto;
  padding: 10px 45px;
  letter-spacing: 1px;
  text-align: center;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
  background-color: #f3ebe6;
  font-family: var(--main-website-font-family);
  font-weight: bold;
  font-size: 20px;
  opacity: 1;
  transition: opacity ease 0.5s;
}
button:hover {
  opacity: 0.8;
  transition: opacity ease 0.5s;
}

/* LOADER */
#cLoader {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#cLoader img {
  width: 35px;
  height: 35px;
  margin: 0 auto;
  padding: 30px 0 10px 0;
}

/* SUCCESS, ERROR BOXES */
.sentBox {
  display: none;
  width: 95%;
  margin-top: 30px;
  padding: 10px;
  text-align: center;
  font-size: 20px;
}
.sentBox a {
  text-decoration: underline;
}
#cSuccess {
  background-color: lightgreen;
  border: 1px solid seagreen;
}
#cError {
  background-color: lightcoral;
  border: 1px solid firebrick;
}

@media screen and (max-width: 1100px) {

  .contactWrapper {
    width: 90%;
  }

  form {
    width: 85%;
  }

}

@media screen and (max-width: 800px) {

  .contactWrapper {
    width: 90%;
  }

  form {
    width: 90%;
  }

  .cTitle {
    font-size: 50px;
  }

  button {
    font-size: 15px;
  }

}

@media screen and (max-width: 500px) {

  .contactWrapper {
    margin: 0 auto;
    padding: 0 0 30px 0;
  }

  .contactBG {
    padding: 0;
  }

  .contactWrapper {
    width: 100%;
  }

  form {
    width: 90%;
  }

  .cSubtitle {
    width: 80%;
  }

}

</style>