<template>

<div v-if="home != 'home'" class="navBarColored">
  <ViewDesktopNavBar v-if="desktopView"
                     nav-bar-color="background-color: var(--nav-bar-bg-color-header); color: var(--nav-bar-font-color-two);"
                     sub-points-bg-color="background-color: var(--sub-points-background-color-two);"
                     :logo-url="logoUrlDark"
                     id="menuColored"
  ></ViewDesktopNavBar>
  <ViewMobileNavBar v-else :logo-url="logoUrlDark"></ViewMobileNavBar>
</div>

<div v-else>
  <!-- Todo: Samsung internet problem: does not calculate desktopView on mounted; but when starting scrolling -.- | desktopView == false -->
  <ViewDesktopNavBar v-if="desktopView"
                     nav-bar-color="background-color: var(--nav-bar-bg-transparent); color: var(--nav-bar-font-color-one);"
                     sub-points-bg-color="background-color: var(--sub-points-background-color-one);"
                     :logo-url="logoUrlLight"
  ></ViewDesktopNavBar>
  <ViewMobileNavBar v-else home="home" :logo-url="logoUrlLight"></ViewMobileNavBar>
</div>

</template>

<script>

import ViewDesktopNavBar from "@/components/navBar/desktop/ViewDesktopNavBar";
import ViewMobileNavBar from "@/components/navBar/mobile/ViewMobileNavBar";

export default {
  name: "NavBar",
  props: [
    "home"
  ],
  data() {
    return {
      desktopView: undefined,
      logoUrlDark: window.location.origin + '/logo/logo-dark.png',
      logoUrlLight: window.location.origin + '/logo/logo.png',
    }
  },
  components: {
    ViewDesktopNavBar,
    ViewMobileNavBar,
  },
  mounted() {
    let vw = window.innerWidth;
    if (vw > 1023) { this.desktopView = true; }
    else           { this.desktopView = false; }

    window.addEventListener('resize', () => {
      vw = window.innerWidth;
      if (vw > 1023) { this.desktopView = true; }
      else           { this.desktopView = false; }
    });
  }
}

</script>

<style scoped>

.navBarColored {
  height: var(--nav-bar-height);
}

#menuColored {
  box-shadow: 0 5px 15px lightgrey;
}

</style>