<template>
  <div class="navBar">

    <div v-if="home != 'home'" id="header" class="header">
      <MobileMenuLogo v-bind:logo-image-src="logoUrl"></MobileMenuLogo>
      <MobileMenuHamburgerDark v-on:click.native="toggleMenu"></MobileMenuHamburgerDark>
    </div>

    <div v-else class="header">
      <MobileMenuLogo v-bind:logo-image-src="logoUrl"></MobileMenuLogo>
      <MobileMenuHamburgerTwoColors v-on:click.native="toggleMenu"></MobileMenuHamburgerTwoColors>
    </div>

    <div id="menuMobile" ref="menuMobile">
      <MobileMenuHead></MobileMenuHead>
      <MobileMenuMain></MobileMenuMain>
      <MobileMenuFoot></MobileMenuFoot>
    </div>

  </div>

</template>

<script>

import MobileMenuFoot from "@/components/navBar/mobile/MobileMenuFoot";
import MobileMenuHead from "@/components/navBar/mobile/MobileMenuHead";
import MobileMenuMain from "@/components/navBar/mobile/MobileMenuMain";
import MobileMenuLogo from "@/components/navBar/mobile/MobileMenuLogo";
import MobileMenuHamburgerDark from "@/components/navBar/mobile/MobileMenuHamburgerOneColor";
import MobileMenuHamburgerTwoColors from "@/components/navBar/mobile/MobileMenuHamburgerTwoColors";

export default {
  name: "ViewMobileNavBar",
  props: [
      "logoUrl",
      "home"
  ],
  components: {
    MobileMenuHamburgerTwoColors,
    MobileMenuHamburgerDark,
    MobileMenuLogo,
    MobileMenuMain,
    MobileMenuHead,
    MobileMenuFoot
  },
  mounted() {

    // SCROLL BEHAVIOUR
    document.documentElement.style.overflow = "visible"; // html scroll
    document.body.style.overflow ="visible";             // body scroll

    // CALCULATE & SET INITIAL VH VALUE
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);

    // ADD LISTENER FOR VH VALUE IN CASE OF WINDOW RESIZING
    window.addEventListener('resize', () => {
      let vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
      if (window.innerWidth > 1023) {
        document.documentElement.style.overflow = "visible"; // html scroll
        document.body.style.overflow = "visible";            // body scroll
      }
    });
  },
  methods: {
    toggleMenu() {

      let toggle = document.getElementById("hamburger")
      let menu = document.getElementById("menuMobile")
      let animatedMenuPoints = document.getElementsByClassName("animate")
      let html = document.documentElement
      let body = document.body

      if (toggle.classList.contains("is-active")) {
        toggle.classList.remove("is-active")
        menu.style.display = "none"
        html.style.overflow = "visible"
        body.style.overflow ="visible"
        for (let i = 0; i < animatedMenuPoints.length; i++) {
          animatedMenuPoints[i].classList.remove("move")
        }
      } else {
        toggle.classList.add("is-active")
        menu.style.display = "block"
        html.style.overflow = "hidden"
        body.style.overflow = "hidden"
        for (let i = 0; i < animatedMenuPoints.length; i++) {
          animatedMenuPoints[i].classList.add("move")
        }
      }
    }
  }
}

</script>

<style scoped>

.header {
  height: var(--nav-bar-height);
  position: absolute;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#header {
  background-color: var(--nav-bar-bg-color-header);
  box-shadow: 0 5px 15px lightgrey;
}

.header a {
  display: flex;
  align-items: center;
}

#menuMobile {
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  display: none;
  position: absolute;
  width: 100%;
  margin: auto;
  overflow: hidden;
  z-index: 1000;
  letter-spacing: var(--menu-points-letter-spacing);
}

</style>