<template>

<div class="wholeWrapper">
  <div class="iframeWrapper">
    <svg class="foregroundSVG" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-on:click="playVideo($event, video.videoWrapperID, video.thumbnailID)">
      <!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. -->
      <path fill="white" d="M512 256C512 397.4 397.4 512 256 512C114.6 512 0 397.4 0 256C0 114.6 114.6 0 256 0C397.4 0 512 114.6 512 256zM176 168V344C176 352.7 180.7 360.7 188.3 364.9C195.8 369.2 205.1 369 212.5 364.5L356.5 276.5C363.6 272.1 368 264.4 368 256C368 247.6 363.6 239.9 356.5 235.5L212.5 147.5C205.1 142.1 195.8 142.8 188.3 147.1C180.7 151.3 176 159.3 176 168V168z"/>
    </svg>
    <img :id="video.thumbnailID" :src="video.thumbnailSrc" :alt="video.thumbnailAlt">
    <div class="videoWrappers" :id="video.videoWrapperID">
      <youtube ref="youtube" :video-id="video.videoYTID" width="1000" height="563" :fit-parent="fitParent" :resize="resize" :resize-delay="resizeDelay" @ready="ready(video.bgSVGID, video.videoWrapperID)"></youtube>
    </div>
  </div>
  <div class="videoDescription">
    <img class="loaderGif" :id="video.bgSVGID" src="/img/loading.gif" alt="GIF Loader">
    <div class="videoTitle">{{ video.videoTitle }}</div>
    <!-- <hr> -->
    <div style="height: 10px;"></div>
    <div class="videoSubtitle">{{ video.videoSubtitle }}</div>
  </div>
</div>

</template>

<script>

export default {
  name: "VidBoxYT",
  props: [
      "video"
  ],
  data() {
    return {
      fitParent: true,
      resize: true,
      resizeDelay: 0,
      noCookie: true,
    }
  },
  computed: {
    player() {
      return this.$refs.youtube.player
    }
  },
  methods: {
    ready: function(bgSVGID, videoWrapperID) {
      document.getElementById(bgSVGID).style.display = "none";
      document.getElementById(videoWrapperID).style.display = "block";
    },
    playVideo: function(event, videoWrapperID, thumbnailID) {
      event.target.style.display = "none";
      document.getElementById(thumbnailID).style.display = "none";
      let wrapper = document.getElementById(videoWrapperID);
      wrapper.style.display = "block";
      this.$refs.youtube.player.playVideo()
    },
  },
}

</script>

<style scoped>

.wholeWrapper {
  padding-top: 10px;
  background-color: white;
}

.iframeWrapper {
  width: 100%;
  height: 100%;
  position: relative;
  max-width: 1280px;
  max-height: 720px;
  margin: 0 auto;
  background-color: white;
}

.iframeWrapper img {
  position: absolute;
  width: 100%;
  height: 100%;
  max-width: 1280px;
  max-height: 720px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  z-index: 15;
  box-shadow:  -2vw 0 0 white, 2vw 0 0 white, 0 1vw 0 white, 0 -2vw 0 white;
}

.videoWrappers {
  display: none;
}

.loaderGif {
  margin: auto;
  padding: 5% 0;
}

.foregroundSVG {
  position: absolute;
  width: 100%;
  height: 20%;
  top: 40%;
  z-index: 50;
  cursor: pointer;
}

.videoDescription {
  text-align: center;
}

.videoTitle {
  margin-top: 25px;
  font-size: 40px;
}

.videoSubtitle {
  text-transform: uppercase;
  font-size: 1.5vw;
  padding-bottom: 10px;
}

hr {
  margin: 10px auto;
  width: 70%;
}

@media screen and (max-width: 1400px) {

  .iframeWrapper  {
    max-width: 80%;
    margin: 0 auto;
  }

  .videoTitle {

  }

  .videoSubtitle {
    font-size: 2vw;
  }

}

@media screen and (max-width: 850px) {

  .videoTitle {

  }

  .videoSubtitle {
    font-size: 2.5vw;
  }

}

@media screen and (max-width: 600px) {

  .iframeWrapper  {
    max-width: 90%;
    margin: 0 auto;
  }

  .loaderGif {
    height: 32px;
  }

  .videoTitle {
    font-size: 30px;
  }

  .videoSubtitle {
    font-size: 20px;
  }

}

</style>