  <div class="cookieConsent" slot="message">{{ message }}
    <router-link :to="moreInfoLink">{{ moreInfo }}</router-link>
  </div>
</cookie-law>

</template>

<script>

import CookieLaw from 'vue-cookie-law'

export default {
  name: "CookieConsent",
  components: { CookieLaw },
