      moreInfoLink: "/datenschutz",
      message: "Cookies erleichtern die Bereitstellung unserer Dienste. Mit der Nutzung unserer Dienste bist du damit einverstanden, dass wir Cookies verwenden. ",
    }
  }
}

</script>

<style scoped>

.cookieConsent {
  font-family: var(--main-website-font-family);
