import Vue from 'vue'                                             // Vue
import App from './App.vue'                                       // App Entrypoint

import VueRouter from 'vue-router'                                // Vue Router
import router from "./router"                                     // Router Pages

import VueYoutube from 'vue-youtube'                              // Wrapper YouTube API

import AOS from 'aos';                                            // Animation on Scroll Framework
import 'aos/dist/aos.css';

AOS.init();                                                       // Initialize AOS to use anywhere

//import 'fullpage.js/vendors/scrolloverflow'                     // FullPage JS Scrolloverflow
//import VueFullPage from 'vue-fullpage.js'                       // FullPage JS

//import './assets/icons'                                           // Registered Icons
//import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'    // Font Awesome Component

Vue.use(VueRouter);                                               // Register Router
//Vue.use(VueFullPage);                                           // Register FullPage
Vue.use(VueYoutube);                                              // Register YouTube API Wrapper

//Vue.component('font-awesome-icon', FontAwesomeIcon)            // Register <font-awesome-icon> Component

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router
}).$mount('#app')
